.loginbox {
  min-height: 60%;
  max-height: fit-content;
  width: 30%;
}

.navbarlogin {
  position: absolute;
  width: 5%;
  top: 0;
  right: 0;
}

input {
  width: 100%;
  height: 50px;
  border: none;
  outline: none;
  padding: 7px;
  background: none;
  font-size: 18px;
}

.link {
  color: rgb(12, 245, 90);
  font-size: 18px;
  font-weight: 400;
}
.link2 {
  color: rgb(0, 17, 255);
  font-size: 18px;
  font-weight: 400;
}
.note {
  background-color: rgba(235, 248, 47, 0.7);
  backdrop-filter: blur(20px);
}

